export default class FooterMarkup {
  getFooterMarkup(year) {
    return `<section class="copyright">
    <h2 class="visually-hidden"> </h2>
    <p class="copyright__text">  <span class="copyright__participant"></span><span class="copyright__participant copyright__participant--green-color"></span><span class="copyright__participant copyright__participant--gold-color"></span>.</p>
    <p class="copyright__text">   2019-${year}&#169;</p>
  </section>
    `;
  }
}
